import { message } from 'antd';
import { useEffect, useState } from 'react';
import { useAppTranslation } from 'src/app/providers/i18n';
import { CreateTransactionData, MockAPI, UpdateTransactionData } from 'src/shared/api/mock';
import { TransactionFormData } from '../../form';
import { TransactionModalMode, UseTransactionModalResult } from './types';

export const useTransactionModal = (): UseTransactionModalResult => {
  const { t } = useAppTranslation();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalMode, setModalMode] = useState<TransactionModalMode>('create');
  const [loading, setLoading] = useState(false);
  const [editingTransactionId, setEditingTransactionId] = useState<string | null>(null);

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–∞—Ä—Å–∏–Ω–≥–∞ —Ö–µ—à–∞
  const parseHash = (hash: string) => {
    if (hash === '#create') {
      return { mode: 'create' as TransactionModalMode, id: null };
    }

    const editMatch = hash.match(/^#edit\/(.+)$/);
    if (editMatch) {
      return { mode: 'edit' as TransactionModalMode, id: editMatch[1] };
    }

    return null;
  };

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —Ö–µ—à–∞
  const updateHash = (mode: TransactionModalMode, id?: string) => {
    if (mode === 'create') {
      window.location.hash = 'create';
    } else if (mode === 'edit' && id) {
      window.location.hash = `edit/${id}`;
    }
  };

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è —Ö–µ—à–∞
  const clearHash = () => {
    if (window.location.hash) {
      // –£–¥–∞–ª—è–µ–º —Ö–µ—à –±–µ–∑ –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
      const url = window.location.pathname + window.location.search;
      window.history.replaceState({}, document.title, url);
    }
  };

  // –≠—Ñ—Ñ–µ–∫—Ç –¥–ª—è –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏–π —Ö–µ—à–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –∏ navigation
  useEffect(() => {
    const handleHashChange = () => {
      const hash = window.location.hash;
      const parsedHash = parseHash(hash);

      if (parsedHash) {
        if (parsedHash.mode === 'create') {
          setModalMode('create');
          setEditingTransactionId(null);
          setIsModalOpen(true);
        } else if (parsedHash.mode === 'edit' && parsedHash.id) {
          // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —Å—É—â–µ—Å—Ç–≤—É–µ—Ç –ª–∏ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—è —Å —Ç–∞–∫–∏–º ID
          const transaction = MockAPI.getTransactionById(parsedHash.id);
          if (transaction) {
            setModalMode('edit');
            setEditingTransactionId(parsedHash.id);
            setIsModalOpen(true);
          } else {
            // –ï—Å–ª–∏ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—è –Ω–µ –Ω–∞–π–¥–µ–Ω–∞, –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –æ—à–∏–±–∫—É –∏ —É–¥–∞–ª—è–µ–º —Ö–µ—à
            message.error(t('transaction.modal.error.notFound'));
            clearHash();
          }
        }
      } else if (hash === '') {
        // –ï—Å–ª–∏ —Ö–µ—à —É–¥–∞–ª–µ–Ω, –∑–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
        setIsModalOpen(false);
        setEditingTransactionId(null);
        setTimeout(() => {
          setModalMode('create');
        }, 300);
      }
    };

    // –ü—Ä–æ–≤–µ—Ä—è–µ–º —Ö–µ—à –ø—Ä–∏ –ø–µ—Ä–≤–æ–π –∑–∞–≥—Ä—É–∑–∫–µ
    handleHashChange();

    // –°–ª—É—à–∞–µ–º –∏–∑–º–µ–Ω–µ–Ω–∏—è —Ö–µ—à–∞
    window.addEventListener('hashchange', handleHashChange);

    return () => {
      window.removeEventListener('hashchange', handleHashChange);
    };
  }, [t]);

  // –û—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏
  const openCreateModal = () => {
    setModalMode('create');
    setEditingTransactionId(null);
    setIsModalOpen(true);
    updateHash('create');
  };

  // –û—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –¥–ª—è —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏
  const openEditModal = (transactionId: string) => {
    setModalMode('edit');
    setEditingTransactionId(transactionId);
    setIsModalOpen(true);
    updateHash('edit', transactionId);
  };

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  const closeModal = () => {
    setIsModalOpen(false);
    setEditingTransactionId(null);
    clearHash();
    // –ù–µ–±–æ–ª—å—à–∞—è –∑–∞–¥–µ—Ä–∂–∫–∞ –ø–µ—Ä–µ–¥ —Å–±—Ä–æ—Å–æ–º —Å–æ—Å—Ç–æ—è–Ω–∏—è –¥–ª—è –ø–ª–∞–≤–Ω–æ–π –∞–Ω–∏–º–∞—Ü–∏–∏
    setTimeout(() => {
      setModalMode('create');
    }, 300);
  };

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
  const handleSubmit = async (data: TransactionFormData): Promise<void> => {
    setLoading(true);

    try {
      // –ò–º–∏—Ç–∏—Ä—É–µ–º –∑–∞–¥–µ—Ä–∂–∫—É API –∑–∞–ø—Ä–æ—Å–∞
      await new Promise((resolve) => setTimeout(resolve, 500));

      if (modalMode === 'create') {
        // –°–æ–∑–¥–∞–µ–º –Ω–æ–≤—É—é —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—é —á–µ—Ä–µ–∑ API
        const createData: CreateTransactionData = {
          type: data.type,
          amount: data.amount,
          title: data.title,
          description: data.description,
          categoryName: data.categoryName,
          categoryIcon: data.categoryIcon,
          categoryColor: data.categoryColor,
          date: data.date,
          tags: data.tags,
        };

        const newTransaction = MockAPI.createTransaction(createData);
        console.log('–°–æ–∑–¥–∞–Ω–∞ –Ω–æ–≤–∞—è —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—è:', newTransaction);
        message.success(t('transaction.modal.messages.createSuccess'));
      } else if (modalMode === 'edit' && editingTransactionId) {
        // –û–±–Ω–æ–≤–ª—è–µ–º —Å—É—â–µ—Å—Ç–≤—É—é—â—É—é —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—é —á–µ—Ä–µ–∑ API
        const updateData: UpdateTransactionData = {
          id: editingTransactionId,
          type: data.type,
          amount: data.amount,
          title: data.title,
          description: data.description,
          categoryName: data.categoryName,
          categoryIcon: data.categoryIcon,
          categoryColor: data.categoryColor,
          date: data.date,
          tags: data.tags,
        };

        const updatedTransaction = MockAPI.updateTransaction(editingTransactionId, updateData);

        if (updatedTransaction) {
          console.log('–û–±–Ω–æ–≤–ª–µ–Ω–∞ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—è:', updatedTransaction);
          message.success(t('transaction.modal.messages.updateSuccess'));
        } else {
          throw new Error('–¢—Ä–∞–Ω–∑–∞–∫—Ü–∏—è –Ω–µ –Ω–∞–π–¥–µ–Ω–∞');
        }
      }

      closeModal();

      // –ü—Ä–∏–Ω—É–¥–∏—Ç–µ–ª—å–Ω–æ –æ–±–Ω–æ–≤–ª—è–µ–º —Å—Ç—Ä–∞–Ω–∏—Ü—É –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏–π
      // –í —Ä–µ–∞–ª—å–Ω–æ–º –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏ –ª—É—á—à–µ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å state management –∏–ª–∏ callback
      window.location.reload();
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–∏ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏:', error);

      const errorMessage =
        modalMode === 'create'
          ? t('transaction.modal.messages.createError')
          : t('transaction.modal.messages.updateError');

      message.error(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  return {
    isModalOpen,
    modalMode,
    loading,
    editingTransactionId,
    openCreateModal,
    openEditModal,
    closeModal,
    handleSubmit,
  };
};
